.resume-welcome .resume-bg{
    background-image: url('../../images/backgrounds/resume-bg-one.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    padding: 115px 0;
}

.resume-welcome .section-title h4{
    font-size: 50px;
}

.resume-welcome .section-title p{
    max-width: 620px;
}

.resume-welcome-text .button{
    width: 320px;
    display: block;
    margin: 0 auto;
}

.resume-media{
    padding-bottom: 40px;
}

@media screen and (max-width: 991px){
    .resume-welcome .resume-bg{
        padding: 70px 0;
    }
    .resume-welcome .section-title h4{
        font-size: 42px;
    }
}

@media screen and (max-width: 575px){    
    .resume-welcome .section-title h4{
        font-size: 28px;
    }
}